<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="">
      </div>
      <!-- 登录表单区域 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- 用户名 -->
        <el-form-item prop="user_id">
          <el-input v-model="loginForm.user_id" prefix-icon="iconfont icon-user" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="passwd">
          <el-input v-model="loginForm.passwd" prefix-icon="iconfont icon-3702mima" type="password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <!-- 验证码 -->
        <el-form-item>
          <el-col :span="11">
            <img :src="imgCode.code" alt="" style="width: 168px;height: 40px;" @click="loadVerify">
          </el-col>
          <el-col :span="11">
            <el-input v-model="loginForm.keycode" placeholder="请输入验证码"></el-input>
          </el-col>
          
          
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 这是登录表单的数据绑定对象
      imgCode: {
        code: ''
      },
      loginForm: {
        user_id: '',
        passwd: '',
        keycode: ''
      },
      // 这是表单的验证规则对象
      loginFormRules: {
        // 验证用户名是否合法
        user_id: [
          { required: true, message: '请输入登录名称', trigger: 'blur' },
          { min: 1,max: 1000, message: '请输入登录名称', trigger: 'blur' }
        ],
        // 验证密码是否合法
        passwd: [
          { required: true, message: '请输入登录密码', trigger: 'blur' },
          { min: 1,max: 1000, message: '请输入登录密码', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    //this.loadVerify()
  },
  methods: {
    loadVerify(){
      this.$http.post('login/ValidateCode?w=90&h=33&code=4&line=0',
          null
        ).then(data => {
            this.imgCode.code=data.data.data
        }).catch(err => {
        　　console.log(err)
        })
    },
    // 点击重置按钮，重置登录表单
    resetLoginForm() {
      // console.log(this);
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        if(!(this.loginForm.user_id=='administrator'&&this.loginForm.passwd=='123')) return this.$message.error('账号或者密码有误！')
        this.$message.success('登录成功！')
        window.sessionStorage.setItem('token', "Token")
        this.$router.push('/home');
        // this.$http.post('login/checkLogin',JSON.stringify(this.loginForm)
          
        // ).then(json => {
        // 　　console.log(json)
        // }).catch(err => {
        // 　　console.log(err)
        // })
      })

      /*this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message.error('登录失败！')
        this.$message.success('登录成功')
        // 1. 将登录成功之后的 token，保存到客户端的 sessionStorage 中
        //   1.1 项目中出了登录之外的其他API接口，必须在登录之后才能访问
        //   1.2 token 只应在当前网站打开期间生效，所以将 token 保存在 sessionStorage 中
        window.sessionStorage.setItem('token', res.data.token)
        // 2. 通过编程式导航跳转到后台主页，路由地址是 /home
        this.$router.push('/home')
      })*/
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-color: #878fb4d0;
  height: 100%;
}

.login_box {
  text-align: center;
  width: 450px;
  height: 350px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

// .btns {
//   display: center;
//   justify-content: flex-end;
// }
</style>
