<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>数据统计</el-breadcrumb-item>
      <el-breadcrumb-item>中国地图</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <!-- <el-card> -->
      <!-- 2. 为ECharts准备一个具备大小（宽高）的Dom -->
      <div class="charts" id="chart1"></div>
    <!-- </el-card> -->
  </div>
</template>
<script>
export default {
    data(){
      return {
        //中国地图数据
        mapData:{
            confirm: [],
            dead: [],
            cured: []
        },
      }
    },
    mounted () {
      this.mapData={
        "confirm":[
            {"name":"湖北","value":65187},{"name":"广东","value":1347},{"name":"河南","value":1271},{"name":"浙江","value":1205},{"name":"湖南","value":1016},{"name":"安徽","value":989},
            {"name":"江西","value":934},{"name":"山东","value":756},{"name":"江苏","value":631},{"name":"重庆","value":576},
            {"name":"四川","value":531},{"name":"黑龙江","value":480},{"name":"北京","value":400},{"name":"上海","value":336},{"name":"河北","value":312},
            {"name":"福建","value":294},{"name":"广西","value":252},{"name":"陕西","value":245},{"name":"云南","value":174},{"name":"海南","value":168},
            {"name":"贵州","value":146},{"name":"天津","value":135},{"name":"山西","value":133},{"name":"辽宁","value":121},{"name":"吉林","value":93},
            {"name":"甘肃","value":91},{"name":"香港","value":90},{"name":"新疆","value":76},{"name":"内蒙古","value":75},{"name":"宁夏","value":71},
            {"name":"台湾","value":32},{"name":"青海","value":18},{"name":"澳门","value":10},{"name":"西藏","value":1}],
        "dead":[{"name":"湖北","value":2615},{"name":"广东","value":7},{"name":"河南","value":19},{"name":"浙江","value":1},{"name":"湖南","value":4},
            {"name":"安徽","value":6},{"name":"江西","value":1},{"name":"山东","value":6},{"name":"江苏","value":0},{"name":"重庆","value":6},{"name":"四川","value":3},
            {"name":"黑龙江","value":12},{"name":"北京","value":4},{"name":"上海","value":3},{"name":"河北","value":6},{"name":"福建","value":1},{"name":"广西","value":2},
            {"name":"陕西","value":1},{"name":"云南","value":2},{"name":"海南","value":5},{"name":"贵州","value":2},{"name":"天津","value":3},{"name":"山西","value":0},
            {"name":"辽宁","value":1},{"name":"吉林","value":1},{"name":"甘肃","value":2},{"name":"香港","value":2},{"name":"新疆","value":2},{"name":"内蒙古","value":0},
            {"name":"宁夏","value":0},{"name":"台湾","value":1},{"name":"青海","value":0},{"name":"澳门","value":0},{"name":"西藏","value":0}],
        "cured":[{"name":"湖北","value":20969},{"name":"广东","value":851},{"name":"河南","value":1033},{"name":"浙江","value":867},{"name":"湖南","value":783},
            {"name":"安徽","value":744},{"name":"江西","value":719},{"name":"山东","value":377},{"name":"江苏","value":478},{"name":"重庆","value":373},{"name":"四川","value":307},
            {"name":"黑龙江","value":249},{"name":"北京","value":235},{"name":"上海","value":272},{"name":"河北","value":261},{"name":"福建","value":218},{"name":"广西","value":147},
            {"name":"陕西","value":192},{"name":"云南","value":144},{"name":"海南","value":127},{"name":"贵州","value":104},{"name":"天津","value":96},{"name":"山西","value":104},
            {"name":"辽宁","value":88},{"name":"吉林","value":65},{"name":"甘肃","value":81},{"name":"香港","value":24},{"name":"新疆","value":34},{"name":"内蒙古","value":38},
            {"name":"宁夏","value":65},{"name":"台湾","value":5},{"name":"青海","value":18},{"name":"澳门","value":7},{"name":"西藏","value":1}]}
      this.initMap();
    },
    methods: {
      /**
         * 中国地图
         */
        initMap: function(){
            var mydata = this.mapData;
            var optionMap = {
                backgroundColor:'rgba(128, 128, 128, 0)',
                //backgroundColor: '#FFFFFF',  
                title: {  
                    //text: '国内疫情地图',  
                    subtext: '', 
                    x:'center',
                    textStyle: {
                        color: 'white'
                    }
                }, 
                
                //左侧小导航图标
                visualMap: {  
                    show : true,
                    calculable: false,
                    calculableColor: 'white',
                    orient: 'vertical',
                    x:'right',      //可设定图例在左、右、居中
                    y:'center',     //可设定图例在上、下、居中
                    //padding:[50,255,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离]
                    textStyle: {
                        color: 'white',
                        //fontSize: 25
                    },
                    splitList: [
                        {start:5000},
                        {start: 1000, end:4999},  
                        {start: 500, end: 999},
                        {start: 100, end: 499},  
                        {start: 10, end: 99},
                        {start: 0, end: 9},  
                    ]
                },   
                tooltip : {  
                    trigger: 'item',
                    formatter:function(params){
                        //定义一个res变量来保存最终返回的字符结果,并且先把地区名称放到里面
                        var region=params.name;
                        var flag=true;
                        var res=region+'<br />';
                        //循环遍历series数据系列
                        for(var j=0;j<mydata.confirm.length;j++){
                            if(mydata.confirm[j].name==region){
                                flag=false;
                                res+='确诊:'+mydata.confirm[j].value+'<br />';
                                res+='死亡:'+mydata.dead[j].value+'<br />';
                                res+='治愈:'+mydata.cured[j].value+'<br />';
                            }
                        }
                        if(flag){
                            res+='确诊: 0<br />';
                            res+='死亡: 0<br />';
                            res+='治愈: 0<br />';
                        }
                        //返回res
                        //console.log(res);
                        return res;                
                    },
                },  
                //配置属性
                series: [{  
                    name: '确诊人数',  
                    type: 'map',  
                    mapType: 'china',   
                    roam: true,
                    zoom: 1.2,
                    
                    label: {  
                        normal: {  
                            show: true  //省份名称  
                        },  
                        emphasis: {  
                            show: true  
                        }  
                    },  
                    data:mydata.confirm  //数据
                },{  
                    name: '死亡人数',  
                    type: 'map',  
                    mapType: 'china',   
                    roam: true,
                    zoom: 1.2,
                    
                    label: {  
                        normal: {  
                            show: true  //省份名称  
                        },  
                        emphasis: {  
                            show: true  
                        }  
                    },  
                    data:mydata.dead  //数据
                },{  
                    name: '治愈人数',  
                    type: 'map',  
                    mapType: 'china',   
                    roam: true,
                    zoom: 1.2,
                    
                    label: {  
                        normal: {  
                            show: true  //省份名称  
                        },  
                        emphasis: {  
                            show: true  
                        }  
                    },  
                    data:mydata.cured  //数据
                }]  
            };  
            //初始化echarts实例
            var myChart = this.$echarts.init(document.getElementById('chart1'));
        
            //使用制定的配置项和数据显示图表
            myChart.setOption(optionMap);
        
    }
  }
}
</script>

<style lang="less" scoped>
.charts {
  float: left;
  margin-top: 20px;
  width: 100%;
  height: 500px;
}
</style>