<template>
  <div class="main">
    <el-container class="home-container">
      <!-- 头部区域 -->
      <el-header>
        <div>
          <!-- <img src="../assets/logo.png" alt=""> -->
          <span>宠物氧吧App后台管理系统</span>
        </div>
        <div class="myInfo">
            <el-dropdown placement="bottom-end" size="medium" @command="commandHandler">
              <span><img src="../assets/6.jpg" /></span>
              <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item command="myCenter">个人中心</el-dropdown-item>
                  <el-dropdown-item command="setting" divided>设置</el-dropdown-item>
                  <el-dropdown-item command="logout" divided>注销登录</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
        </div>
      </el-header>
      <!-- 页面主体区域 -->
      <el-container>
        <!-- 侧边栏 -->
        <el-aside :width="isCollapse ? '64px' : '200px'">
          <div class="toggle-button" @click="toggleCollapse"><li class="el-icon-s-operation"></li></div>
          <!-- 侧边栏菜单区域 -->
          <el-menu background-color="#333744" text-color="#fff" active-text-color="#409EFF" unique-opened :collapse="isCollapse" :collapse-transition="false" router :default-active="activePath">
            <el-menu-item index="/welcome" @click="saveNavState('/welcome')">
              <i class="el-icon-s-home"></i>
              <span slot="title">主页</span>
            </el-menu-item>

            <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
              <!-- 一级菜单的模板区域 -->
              <template slot="title">
                <!-- 图标 -->
                <i :class="iconsObj[item.id]"></i>
                <!-- 文本 -->
                <span>{{item.authName}}</span>
              </template>

              <!-- 二级菜单 -->
              <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id" @click="saveNavState('/' + subItem.path)">
                <template slot="title">
                  <!-- 图标 -->
                  <i class="el-icon-menu"></i>
                  <!-- 文本 -->
                  <span>{{subItem.authName}}</span>
                </template>
              </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <!-- 右侧内容主体 -->
        <el-main>
          <!-- 路由占位符 -->
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 左侧菜单数据
      menulist: [
        {
          id: 1,
          authName: "用户管理",
          children:[{
            id: 101,
            authName: "用户列表",
            children:[],
            path: "users",
            order: 1
          }],
          path: "",
          order: 1
        },{
          id: 2,
          authName: "权限管理",
          children:[{
            id: 201,
            authName: "权限列表",
            children:[],
            path: "rights",
            order: 2
          },{
            id: 202,
            authName: "角色列表",
            children:[],
            path: "roles",
            order: 2
          }],
          path: "",
          order: 2
        },{
          id: 3,
          authName: "商品管理",
          children:[{
            id: 301,
            authName: "商品列表",
            children:[],
            path: "goods",
            order: 3
          },{
            id: 302,
            authName: "商品分类",
            children:[],
            path: "categories",
            order: 3
          },{
            id: 303,
            authName: "分类参数",
            children:[],
            path: "params",
            order: 3
          }],
          path: "",
          order: 3
        },{
          id: 4,
          authName: "系统监控",
          children:[{
            id: 401,
            authName: "Echarts学习",
            children:[],
            path: "reports",
            order: 4
          },{
            id: 402,
            authName: "中国地图",
            children:[],
            path: "chinaMap",
            order: 4
          },{
            id: 403,
            authName: "世界地图",
            children:[],
            path: "worldMap",
            order: 4
          },],
          path: "",
          order: 4
        }
      ],
      iconsObj: {
        //'1': 'iconfont icon-user',
        '1': 'el-icon-user',
        '2': 'el-icon-s-check',
        '3': 'el-icon-goods',
        '4': 'el-icon-s-data',
        '5': 'iconfont icon-baobiao'
      },
      // 是否折叠
      isCollapse: true,
      // 被激活的链接地址
      activePath: ''
    }
  },
  created() {
    //this.getMenuList()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    commandHandler(cmd) {
      this.$message('click on item ' + cmd);
      if (cmd == 'logout') {
          this.$confirm('此操作将注销登录, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
          }).then(() => {
              this.logout()
          }).catch(() => {
              this.$message({
                  type: 'info',
                  message: '已取消操作'
              });
          });
      }
    },
    logout() {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    // 获取所有的菜单
    async getMenuList() {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data
      console.log(res)
    },
    // 点击按钮，切换菜单的折叠与展开
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // 保存链接的激活状态
    saveNavState(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    }
  }
}
</script>
<style lang="less" scoped>
.html{
  width: 100%;
  height: 100%;
}
.main{
  width: 100%;
  height: 100%;
}
.home-container {
  width: 100%;
  height: 100%;
}
.el-header {
  background-color: #313233;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}

.el-aside {
  background-color: #333744;
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}

.iconfont {
  margin-right: 10px;
}

.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}

.myInfo {
  cursor: pointer;
}
.myInfo img {
  width: 45px;
  height: 45px;
  border-radius: 35px;
  float: right;
  margin-right: 20px;
  margin-left: 8px;
}
</style>
