          },

          grid: {

              y2: 80

          },

          xAxis : [

              {

                  type : 'time',

                  splitNumber:10

              }

          ],

          yAxis : [

              {

                  type : 'value'

              }

          ],

          series : [

              {

                  name: 'series1',

                  type: 'line',

                  showAllSymbol: true,

                  symbolSize: function (value){

                      return Math.round(value[2]/10) + 2;

                  },

                  data: (function () {

                      var d = [];

                      var len = 0;

                      var now = new Date();

                      var value;

                      while (len++ < 200) {

                          d.push([

                              new Date(2014, 9, 1, 0, len * 10000),

                              (Math.random()*30).toFixed(2) - 0,

                              (Math.random()*100).toFixed(2) - 0

                          ]);

                      }

                      return d;

                  })()

              }

          ]

      };
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
      window.addEventListener("resize",function(){
        myChart.resize();
      });
    }

    /**
     * methods end
     */
  }
}
</script>

